export function deepClone(obj) {
    if(!obj){
        return obj
    }
    let result = typeof  obj.splice === "function" ? [] : {};
    if (obj && typeof obj === 'object') {
        for (let key in obj) {
            if (obj[key] && typeof obj[key] === 'object') {
                result[key] = deepClone(obj[key]);//objectdeepClone,
            } else {
                result[key] = obj[key];//object
            }

        }
        return result;
    }
    return obj;
}